export default([
    {
        path: '/',
        name: 'home',
        component: () => import('@/views/layout/home'),
        children:[
            {
                path:'/myDepartment',
                name: 'myDepartment',
                component: () => import('@/views/person/myDepartment')
            },{
                path:'/personCenter',
                name: 'personCenter',
                component: () => import('@/views/person/personCenter')
            }
        ]
    },
])


