export default([
    {
        path: '/',
        name: 'home',
        component: () => import('@/views/layout/home.vue'),
        children:[
            {
                path: '/user',
                name: 'user',
                component: () => import('@/views/user/user'),
                meta: {auth: true}
            }
        ]
    },
    
])


