export default([
    {
        path: '/',
        name: 'home',
        component: () => import('@/views/layout/home'),
        children:[
            {
                path:'/hello',
                name: 'hello',
                component: () => import('@/components/HelloWorld')
            },{
                path: '/',
                name: '/workdest',
                component: () => import('@/views/layout//workdest')
            },{
                path:'/test',
                name: 'test',
                component: () => import('@/views/user/test')
            },{
                path:'/roleList',
                name: '角色列表',
                component: () => import('@/views/role/roleList')
            },{
                path:'/roleDetail',
                name: '角色详情',
                component: () => import('@/views/role/roleDetail')
            },{
                path:'/permissionsList',
                name: '路由列表',
                component: () => import('@/views/permissions/permissionsList')
            },{
                path:'/permissionsDetail',
                name: '路由详情',
                component: () => import('@/views/permissions/permissionsDetail')
            },{
                path:'/userList',
                name: '用户列表',
                component: () => import('@/views/user/userList')
            },{
                path:'/userDetail',
                name: '用户详情',
                component: () => import('@/views/user/userDetail')
            },{
                path:'/departmentList',
                name: '部门管理',
                component: () => import('@/views/department/departmentList')
            },{
                path:'/workdest',
                name: '工作台',
                component: () => import('@/views/layout/workdest')
            },
        ]
    },
])


